const View: React.FC = () => {
    const [collapsed, setCollapsed] = useState(false);
    let [breadcrumbList,setBreadcrumbList] = useState<string[]>([]);

    const onMenuClick = (e: any) => {
        const breadcrumbList = getBreadcrumbList(e.key, menuList)
        setBreadcrumbList(breadcrumbList);
    }

    return (
        <Layout>
            <Sider collapsible theme="light" collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}>
                <SiderMenu menus={menuList} onClick={onMenuClick}></SiderMenu>
            </Sider>
            <Layout className="site-layout">
                <Content style={{ margin: '0 16px' }}>
                    <BreadCrumb data={breadcrumbList}></BreadCrumb>
                    <div style={{ minHeight: 360 }}>
                        <Outlet />
                    </div>
                </Content>
                <Footer style={{ textAlign: 'center' }}>React + Ant Design</Footer>
            </Layout>
        </Layout>
    );
};

export default View;