import { useState } from 'react';
import type { MenuProps } from 'antd';
import { Menu } from 'antd';
import { useNavigate, useLocation } from 'react-router-dom';

// 定义 menu 类型
export type MenuItem = Required<MenuProps>["items"][number];

type SiderMenuProps = MenuProps & {
    menus: MenuItem[];
    onClick?: (e: any) => void;
    onOpenChange?: (v: string[]) => void;
};

const SiderMenu: React.FC<SiderMenuProps> = ({ menus, ...props }: SiderMenuProps) => {
    const navigateTo = useNavigate();
    let firstOpenKey: string = '';
    
    const [openKeys, setOpenKeys] = useState([firstOpenKey]);
    const currentRoute = useLocation();
    function findKey(obj: { key: string }) {
        return obj.key === currentRoute.pathname;
    }
    for (let i = 0; i < menus.length; i++) {
        if (menus[i]!['children'] && menus[i]!['children'].length > 1 && menus[i]!['children'].find(findKey)) {
            firstOpenKey = menus[i]!.key as string;
            break;
        }
    }    

    const menuClick = (e: any) => {
        if (props.onClick) {
            props.onClick(e);
        }
        navigateTo(e.key);
    };

    const handleOpenChange = (keys: string[]) => {
        if (props.onOpenChange) {
            props.onOpenChange(keys);
        }
        setOpenKeys([keys[keys.length - 1]]);
    };

    return (<Menu defaultSelectedKeys={[currentRoute.pathname]} mode="inline" items={menus} onClick={menuClick} onOpenChange={handleOpenChange} openKeys={openKeys} />);
};

export default SiderMenu;