import { Breadcrumb } from "antd";

const BreadCrumb = ({ data }: any) => {
    return (
        <Breadcrumb style={{ margin: '16px 0' }}>
            {data.map((item: string) => {
                return <Breadcrumb.Item key={item}>{item}</Breadcrumb.Item>;
            })}
        </Breadcrumb>
    );
};

export default BreadCrumb;